import logo from './logo.svg';
import './App.css';
import React, { useState } from 'react';

let label = '?'

let count = 0;



function App() {
	let  [globalState, setState] = useState({count:0, label:'?'})

const clickButton = (e) => {
	count++
	if(count===3)
	{
		label = 'WIN'
	}
	else
	{
		label = 'LOSE'
	}
	setState({...globalState, label})
}

  return (
    <div className="App">
		<div>
				<span>Enter a number</span>
				<input type='text' ></input>
				<button onClick={(e) => clickButton(e)}>Go</button>
		</div>
		<div>{globalState.label}</div>
    </div>
  );
}

export default App;
